<template>
  <div class="betterscroll"
       ref="betterscrollall">
    <slot name="betterscrollcontent"></slot>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {
      message: 'betterscroll'
    }
  },
  created () {
    let _this = this
    setTimeout(_this._initBetterscroll, 1000) // DOM
  },
  methods: {
    _initBetterscroll () {
      let wrap = this.$refs.betterscrollall
      let _this = this
      this.bscroll = new BScroll(wrap, {
        probeType: 3, // 
        pullUpLoad: {
          threshold: 0 // 
        },
        pullDownRefresh: {
          threshold: 20, // 
          stop: 20 // 
        },
        scrollY: true, // false 
        click: true
      })
      /*  */
      this.bscroll.on('pullingUp', function () {
        // todo
        _this.$emit('shangla', true)
        this.bscroll.finishPullUp() // 
        this.bscroll.refresh() // 
        // bscroll.closePullUp() // 
      })
      /*  */
      /*  */
      this.bscroll.on('pullingDown', function () {
        // todo
        console.log('')
        this.bscroll.finishPullDown() // 
        this.bscroll.refresh() // 
        // bscroll.closePullDown() // 
        _this.$emit('xiala', true)
      })

      /*  */
    }
  }

}
</script>

<style scoped lang="less">
.betterscroll {
  position: absolute;
  top: 1.76rem;
  bottom: 0px;
  width: 100%;
  overflow: hidden;
}
</style>



